@mixin flex-center() {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin flex-between() {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

@mixin flex-start() {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

@mixin flex($justify) {
    display: flex;
    align-items: center;
    justify-content: $justify;
}

@mixin btn() {
    font-weight: 700;
    color: #fff;
    padding: 12px 30px;
    background: $primary;
    border-radius: 3.125rem;
    text-align: center;
    border: 2px solid transparent;

    &:hover {
        cursor: pointer;
        color: $primary;
        border: 2px solid $primary;
        background: transparent;
        transition: background 200ms ease;
    }
}